import React from 'react';
import { styled } from 'styled-components';

const StyleProGress = styled.div`
    position:relative;
    width:${props=>props.$width};
    height:${props=>props.$height};
    display: flex;
    > span {
        position:absolute;
        right:0rem;
        font-size:${props => props.$fontSize};
        top:${props => props.$top};
    }
`

const ProGressBar = styled.div`
    width:${props => `${Math.floor(props.$proGressNum < props.$comPleteNum ? props.$proGressNum / props.$comPleteNum * 100 : 100)}%`}; 
    height:100%;
    background-color:${props => 
        props.$error || props.$proGressNum > props.$comPleteNum
        ? 'var(--error)'
        : 'green'};
    transition: all 0.3s;
`

/**
 * 
 * @param {string} width - ProGress   
 * @param {string} height - ProGress   
 * @param {string} fontSize -     16px
 * @param {string} top -      -1.4rem
 * @param {number} comPleteNum -   
 * @param {number} proGressNum -   
 * @param {any} error -    
 * @returns 
 */

export default function ProGress({
    width,
    height,
    fontSize = '16px',
    top = '-1.4rem',
    comPleteNum,
    proGressNum,
    error,
    ...rest
}) {
    return (
        <StyleProGress
            $width={width}
            $height={height}
            $fontSize={fontSize}
            $top={top}
            {...rest}
        >
            <span>{`${proGressNum} / ${comPleteNum}`}</span>
        </StyleProGress>
    );
}

