import React from 'react';
import { styled } from 'styled-components';

const StyleErrorInput = styled.div`
  margin: 5px 0 5px 0;
  div {
    color: var(--error);
    padding: 5px;
  }
`;

const Label = styled.label`
  color: ${(props) => (props.$labelColor ? props.$labelColor : 'var(--black-100)')};
  font-size: 1.6rem;
  background-color: transparent;
`;

const StyleInput = styled.input`
  margin-top: 7px;
  background: none;
  border: 1px solid;
  font-size: ${(props) => props.$fontSize};
  border-color: ${(props) => (props.$borderColor ? props.$borderColor : 'var(--black-100)')};
  border-radius: ${(props) => (props.$borderRadius ? props.$borderRadius : '3px')};
  width: ${(props) => props.$width};
  height: ${(props) => props.$height};
  color: ${(props) => (props.$color ? props.$color : 'var(--black-100)')};
  padding: 13px;
  font-size: ${(props) => (props.$fontSize ? props.$fontSize : '1.5rem')};
`;

const StyleError = styled.div`
  width: fit-content;
  font-size: 1.5rem;
  height: 1rem;
  opacity: ${(props) => (props.error === '' ? 0 : 1)};
`;

const StyleTextArea = styled.textarea`
  margin-top: 7px;
  background: none;
  border: 1px solid;
  border-color: ${(props) => (props.$borderColor ? props.$borderColor : 'var(--black-100)')};
  border-radius: ${(props) => (props.$borderRadius ? props.$borderRadius : '3px')};
  font-size: ${(props) => props.$fontSize};
  width: ${(props) => props.$width};
  min-height: ${(props) => props.$height};
  color: ${(props) => (props.$color ? props.$color : 'var(--black-100)')};
  padding: 8px 6px;
  font-size: 1.5rem;
  font-family: var(--nanum);
  line-height:1.3
`;

/**
 *    .
 *      ...rest    .
 * @param {string} label -   .
 * @param {string} error -   .
 * @param {string} width -  .
 * @param {string} height -  .
 * @param {string} fontSize -   .
 * @param {string} color -  .   --black-100
 * @param {string} labelColor -   .   --black-100
 * @param {string} borderColor -   border .   --black-100
 * @param {function} onChangeHandler -   onChange  .
 * @param {string} type -  text,textarea     text textarea .
 * @param {any} rest -       .
 * @returns {JSX.Element}
 */

export default function Input({
  label,
  error,
  name,
  width,
  height,
  color,
  fontSize,
  labelColor,
  borderColor,
  borderRadius,
  onChangeHandler,
  type,
  ...rest
}) {
  return (
    <StyleErrorInput className="col">
      <Label htmlFor={name} $labelColor={labelColor}>
        {label}
      </Label>
      {type === 'textarea' ? (
        <StyleTextArea
          rows={1}
          $width={width}
          $height={height}
          $color={color}
          $fontSize={fontSize}
          $borderColor={borderColor}
          $borderRadius={borderRadius}
          onChange={onChangeHandler}
          {...rest}
        />
      ) : (
        <StyleInput
          $width={width}
          $height={height}
          $color={color}
          $fontSize={fontSize}
          $borderColor={borderColor}
          $borderRadius={borderRadius}
          onChange={onChangeHandler}
          type={type}
          {...rest}
        />
      )}
      <StyleError $error={error}>{error}</StyleError>
    </StyleErrorInput>
  );
}
